.customList{
  transition: 10s ease-in-out;
  background:linear-gradient(to right,#6753e9,#a991ff);
  position: relative;
}

.customList::after{
  content: "";
  height: 0%;
  width: 5px;
  background-color: rgb(255, 255, 255);
  bottom:0 ;
  position: absolute;
  left: 0;
  transition: .3s ease-in-out;
}
.customList:hover::after{
  height: 100%;
}


